import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
// ambiente
import { Ambiente } from './api.ambiente';

@Injectable()
export class ApiContacto {

  private back = '';  // URL to web API
  private contactoUrl = 'envio_contacto.php';  // URL to web API
  // contacto
  private apiSource: Subject<any> = new Subject<any>();
  private apiLoaded$ = this.apiSource.asObservable();

  constructor(private http: Http) {
    this.back = Ambiente;
  }

  public sendContacto(contacto) {
    this.sendContactoExecute(contacto).subscribe((response) => {
      this.apiSource.next(response);
    }, (error) => {
      console.error(error);
    });
  }

  public sendContactoExecute(contacto) {
    let url = this.back + this.contactoUrl;
    let headers = new Headers({ 'Content-Type': 'application/json' });
    return this.http.post(url, contacto, headers).map(this.extractData).catch(this.handleError);
  }

  private extractData(res: Response) {
    let body = res.json();
    return body || {};
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



