import { Component, OnInit } from '@angular/core';

declare var window: any;
@Component({
  selector: 'my-nav',
  template: require('./nav.component.html'),
  styles: [require('./nav.component.scss')]
})
export class NavComponent implements OnInit {
  public isClassVisible = false;
  public small = false;

  public ngOnInit() {
    console.log('Init my-nav');
    // for browsers
    window.document.addEventListener('scroll', (e) => this.detectScroll(e));
  }

  public detectScroll(e) {
    let scrollTop;
    if (window.pageYOffset !== undefined) {
      scrollTop = window.pageYOffset;
    } else {
      scrollTop = document.body.scrollTop;
    }
    console.log(scrollTop);
    if (scrollTop > 820) {
      this.small = true;
      document.querySelector('body').classList.add('smallPadding');
    } else {
      this.small = false;
      document.querySelector('body').classList.remove('smallPadding');
    }
  }
}



