import {
    Component, OnInit, AfterViewInit, ElementRef, Input, OnChanges,
    Renderer, ViewChildren, QueryList, HostListener, ChangeDetectorRef,
    TemplateRef, ContentChild
} from '@angular/core';
import { Observable, Subscription } from 'rxjs/Rx';
import { CarouselOptions, CarouselResponsiveOptions, CarouselItem } from './carousel.options';

@Component({
    selector: 'my-carousel',
    template: require('./carousel.component.html'),
    styles: [require('./carousel.component.scss')]
})
export class CarouselComponent implements AfterViewInit, OnInit {

    @ContentChild(TemplateRef)
    public itemTemplate: TemplateRef<any>;

    @Input('items') public items: CarouselItem[] = [];
    @ViewChildren('imgitem') public elements: QueryList<ElementRef>;
    @ViewChildren('carouselwrapper') public wrapper: QueryList<ElementRef>;
    public content = {
        width: 0,
        left: 0,
        widthItem: 0,
        posibleSecuence: 0,
        hasSecuenceImcomplete: false,
        secuenceSlides: []
    };

    @Input('options') public options: CarouselOptions = {
        className: 'carouselcomponent',
        delay: 4000,
        startSlide: 0,
        itemstoshow: 4,
        showControl: true,
        showPaginator: true,
        autoplay: false
    };

    /**
     * Default height de cada item carousel
     * @public
     * @memberOf CarouselComponent
     */
    public slidestyle = { height: '285px' };
    /**
     * Default height del carousel
     * @public
     * @memberOf CarouselComponent
     */
    public placestyle = { height: '285px' };
    /**
     * Slide o secuencia activa
     * @public
     * @memberOf CarouselComponent
     */
    public currentSlide = this.options.startSlide;
    /**
     * Indica el sentido de movimiento del carousel.
     * @private
     * @memberOf CarouselComponent
     */
    private leftToRight = true;
    /**
     * Secuencia activa
     * @private
     * @type {number}
     * @memberOf CarouselComponent
     */
    private secuence: number;
    /**
     * Delay(ms) de evento changeSlide
     * @private
     * @type {Subscription}
     * @memberOf CarouselComponent
     */
    private timer: Subscription;

    // private imageObserver: Observable<any>;
    /**
     * @private
     * @type {Subscription}
     * @memberOf CarouselComponent
     */
    private timerImage: Subscription;

    private lockChange: boolean = false;

    constructor(private slide: ElementRef, private renderer: Renderer,
                private cdRef: ChangeDetectorRef) { }

    @HostListener('mouseenter') public onMouseEnter() {
        if ( typeof this.timer === 'undefined' ) {
            return;
        }
        try {
            this.timer.unsubscribe();
        } catch (e) {
            console.error(e);
        }
    }

    @HostListener('mouseleave') public onMouseLeave() {
        this.initSecuence();
    }

    @HostListener('window:resize', ['$event']) public onResize(event) {
        this.carouselStyles();
        console.log(window.innerWidth);
        // this.placestyle.height = this.elements.first.nativeElement.height + 'px';
        // this.slidestyle.height = this.elements.first.nativeElement.height + 'px';
    }

    public checkCallback(item) {
        if ( typeof this.options.callBackClick !== 'undefined' ) {
            this.options.callBackClick(item);
        }
        return;
    }

    public goToNextSecuenceSlide() {
        let next = this.content.posibleSecuence - 1;
        if (this.currentSlide < (this.content.posibleSecuence - 1)) {
            next = this.currentSlide + 1;
        }
        this.goToSecuenceSlide(next);
    }
    public goToBackSecuenceSlide() {
        let next = 0;
        if (this.currentSlide > 0) {
            next = this.currentSlide - 1;
        }
        this.goToSecuenceSlide(next);
    }

    public goToSecuenceSlide(index: number) {
        // RESPONSIVE UTIL
        let qSlides: number = this.options.itemstoshow;
        if ( typeof this.options.reponsive !== 'undefined') {
            this.options.reponsive.forEach( (resOption: CarouselResponsiveOptions) => {
                if ( window.innerWidth < resOption.maxWidth) {
                    qSlides = resOption.itemstoshow;
                }
            });
        }

        if (index === this.currentSlide) {
            return;
        }
        let leftTo = index * qSlides * this.content.widthItem;
        if (this.content.hasSecuenceImcomplete && index + 1 === this.content.posibleSecuence) {
            let quedan = this.items.length - (index * qSlides);
            leftTo = (this.content.left - quedan * this.content.widthItem) * (-1);
        }
        this.content.left = leftTo * (-1);
        this.currentSlide = index;
        this.secuence = index;
        this.initSecuence();
    }

    public checkLoad() {
        if (typeof this.elements.first !== 'undefined'
            && this.elements.first.nativeElement.naturalHeight > 0) {
            // this.slidestyle.height = this.elements.first.nativeElement.naturalHeight + 'px';
            this.timerImage.unsubscribe();
            this.setStyles();
        }
    }

    public imageLoaded(i) {
        console.log('Image loaded call');
    }

    public ngOnInit() {
        console.log('Init CarouselComponent');
    }

    public ngAfterViewInit() {
        console.log('AfterViewInit CarouselComponent');
        this.secuence = this.options.startSlide;
        this.cdRef.detectChanges(); // Allow changes in view
        let observer = Observable.timer(0, 500);
        this.timerImage = observer.subscribe((r) => this.checkLoad());
    }

    public initCall() {
        alert(this.options.className);
    }

    private setStyles() {
        this.carouselStyles();
        this.initSecuence();
    }

    private initSecuence() {
        if (this.options.autoplay === false
            || (typeof this.timer !== 'undefined' && this.timer.closed === false)) {
            return;
        }
        let observer = Observable.timer(this.options.delay, this.options.delay);
        this.timer = observer.subscribe((r) => this.changeSlide(null));
    }

    private carouselStyles() {
        this.content.left = 0;
        // RESPONSIVE UTIL
        let qSlides: number = this.options.itemstoshow;
        if ( typeof this.options.reponsive !== 'undefined') {
            this.options.reponsive.forEach( (resOption: CarouselResponsiveOptions) => {
                if ( window.innerWidth < resOption.maxWidth) {
                    qSlides = resOption.itemstoshow;
                }
            });
        }
        let width = this.wrapper.first.nativeElement.clientWidth;
        let widthItem = width / qSlides;
        let slidesToShowMax = this.secuence * qSlides + qSlides;
        let slidesToShowMin = this.secuence * qSlides;

        // setear los widthItem
        this.items.forEach((item: CarouselItem, index) => {
            item.width = widthItem;
        });

        // setear width content
        this.content.widthItem = widthItem;
        this.content.width = widthItem * this.items.length;

        // Secuencias posibles
        this.content.hasSecuenceImcomplete = (this.items.length % qSlides) > 0;
        this.content.posibleSecuence = Math.floor(this.items.length / qSlides);
        if (this.content.hasSecuenceImcomplete) {
            this.content.posibleSecuence++;
        }
        this.content.secuenceSlides = new Array(this.content.posibleSecuence);
    }

    private changeSlide(next) {
        // RESPONSIVE UTIL
        let qSlides: number = this.options.itemstoshow;
        if ( typeof this.options.reponsive !== 'undefined') {
            this.options.reponsive.forEach( (resOption: CarouselResponsiveOptions) => {
                if ( window.innerWidth < resOption.maxWidth) {
                    qSlides = resOption.itemstoshow;
                }
            });
        }

        if (typeof next === 'undefined' || next == null) {
            next = this.getNextSecuence();
        }
        let leftTo = next * qSlides * this.content.widthItem;
        if (this.content.hasSecuenceImcomplete && next + 1 === this.content.posibleSecuence) {
            let quedan = this.items.length - (next * qSlides);
            leftTo = (this.content.left - quedan * this.content.widthItem) * (-1);
        }
        this.content.left = leftTo * (-1);
        this.increaseSecuence();
    }

    private getNextSecuence() {
        let next = this.secuence + 1;
        if (next === this.content.posibleSecuence) {
            next = 0;
        }
        return next;
    }
    private increaseSecuence() {
        this.secuence++;
        if (this.secuence === this.content.posibleSecuence) {
            this.secuence = 0;
        }
        this.currentSlide = this.secuence;
    }

}



