import {
    Component, OnInit, AfterViewInit, ElementRef, Input,
    Renderer, ViewChildren, QueryList, HostListener, ChangeDetectorRef,
    ViewEncapsulation
} from '@angular/core';
import { Observable, Subscription } from 'rxjs/Rx';

@Component({
    // tslint:disable-next-line:component-selector-prefix
    selector: 'my-slide',
    template: require('./slide.component.html'),
    styles: [require('./slide.component.scss')],
    encapsulation: ViewEncapsulation.None,
})
export class SlideComponent implements AfterViewInit, OnInit {
    @Input('paginator') public paginator = 'inside';
    @Input('slides') public slides = [];
    @ViewChildren('slideitem') public elements: QueryList<ElementRef>;

    public options = {
        delay: 4000,
        startSlide: 0,
        animateIn: 'fadeIn',
        animateOut: 'fadeOut',
        showPaginator: false
    };
    public slidestyle = { height: '700px' }; // TODO:REMOVER
    public placestyle = { height: '700px' }; // TODO:REMOVER
    public currentSlide = this.options.startSlide;
    private secuence: number;
    private timer: Subscription;

    constructor(public slide: ElementRef, private renderer: Renderer,
                private cdRef: ChangeDetectorRef) { }

    @HostListener('mouseenter')
    public onMouseEnter() {
        this.timer.unsubscribe();
    }

    @HostListener('mouseleave')
    public onMouseLeave() {
        this.initSecuence();
    }

    @HostListener('window:resize', ['$event'])
    public onResize(event) {
        this.placestyle.height = this.elements.first.nativeElement.height + 'px'; // Calculate size
        this.slidestyle.height = this.elements.first.nativeElement.height + 'px'; // Calculate size
    }

    public ngOnInit() {
        console.log('Init SlideComponent');
    }

    public ngAfterViewInit() {
        console.log('AfterViewInit SlideComponent');
        this.secuence = this.options.startSlide;
        this.setStyles();
        this.cdRef.detectChanges(); // Allow changes in view
    }
    public imageLoaded(e) {
        this.slidestyle.height = this.elements.first.nativeElement.height + 'px';
    }

    public goToSlide(index: number) {
        if (index === this.currentSlide) {
            return;
        }
        this.slides[index].style = this.options.animateIn;
        this.slides[this.secuence].style = this.options.animateOut;
        this.secuence = index;
        this.currentSlide = this.secuence;
    }
    public goToNextSecuenceSlide() {
        let next = this.slides.length - 1;
        if (this.currentSlide < next) {
            next = this.currentSlide + 1;
        }
        this.goToSlide(next);
    }

    public goToBackSecuenceSlide() {
        let next = 0;
        if (this.currentSlide > 0) {
            next = this.currentSlide - 1;
        }
        this.goToSlide(next);
    }

    private setStyles() {
        this.slides[this.secuence].style = this.options.animateIn;
        this.initSecuence();
    }

    private initSecuence() {
        let observer = Observable.timer(this.options.delay, this.options.delay);
        this.timer = observer.subscribe((r) => this.changeSlide(r));
    }

    private changeSlide(e) {
        if (this.getNextSecuence() === this.currentSlide) {
            return;
        }
        this.slides[this.getNextSecuence()].style = this.options.animateIn;
        this.slides[this.secuence].style = this.options.animateOut;
        this.increaseSecuence();
    }

    private getNextSecuence() {
        let next = this.secuence + 1;
        if (next === this.slides.length) {
            next = 0;
        }
        return next;
    }
    private increaseSecuence() {
        this.secuence++;
        if (this.secuence === this.slides.length) {
            this.secuence = 0;
        }
        this.currentSlide = this.secuence;
    }
}



