import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { PopupService } from './popup.service';

export interface PopupServiceOptions {
    layout: string;
    action: string;
    show: boolean;
    info: Object;
}

@Component({
    selector: 'my-popup',
    template: require('./popup.component.html'),
    styles: [require('./popup.component.scss')]
})
export class PopupComponent implements OnInit {

    public options: PopupServiceOptions = {
        layout: '',
        action: '',
        show: false,
        info: null
    };
    public listener: Subscription;
    public popupMaxHeight;
    public popupvisibility = false;

    constructor(private popupService: PopupService) { }

    public ngOnInit() {
        console.log('Init PopupComponent');
        this.setPopupMaxHeight();
        this.listener = this.popupService.popup$.subscribe((options: PopupServiceOptions) => {
            this.options = options;
            if (options.show === true) {
                this.popupShow();
            } else {
                this.closePopup();
            }
        });
    }
    public popupShow() {
        this.popupvisibility = true;
    }
    public closePopup() {
        this.popupvisibility = false;
    }
    public setPopupMaxHeight() {
        let wh = window.innerHeight;
        this.popupMaxHeight = wh * 0.6;
    }

}



