import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ApiContacto } from '../shared';
import { Subscription } from 'rxjs/Subscription';

@Component({
    selector: 'my-newssuscription',
    template: require('./newssuscription.component.html'),
    styles: [require('./newssuscription.component.scss')]
})

export class NewssuscriptionComponent implements OnInit {

    public enviando = false;
    public enviadoOk = false;
    public enviadoError = false;
    public model = {
        name: null,
        email: null,
        phone: null
    };
    public newsForm: FormGroup;

    constructor(fb: FormBuilder, private apiService: ApiContacto) {
        this.newsForm = fb.group({
            name: [null],
            email: [null, Validators.required],
            phone: [null]
        });
    }

    public ngOnInit() {
        console.log('Init NewssuscriptionComponent');
    }

    public submitForm(contacto) {
        this.enviando = true;
        this.apiService.sendContactoExecute(contacto).subscribe((response) => {
            this.enviando = false;
            this.enviadoOk = true;
        }, (error) => {
            this.enviando = false;
            this.enviadoError = true;
        });
    }

}



