import { BrowserModule } from '@angular/platform-browser';
import { HttpModule, Http } from '@angular/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {
  NgModule,
  ApplicationRef
} from '@angular/core';
import {
  removeNgStyles,
  createNewHosts,
  createInputTransfer
} from '@angularclass/hmr';
import {
  RouterModule,
  PreloadAllModules
} from '@angular/router';

/*
 * Platform and Environment providers/directives/pipes
 */
import { ENV_PROVIDERS } from './environment';
import { ROUTES } from './app.routes';
// App is our top level component
import { AppComponent } from './app.component';
import { APP_RESOLVER_PROVIDERS } from './app.resolver';
import { AppState, InternalStateType } from './app.service';
import { NavComponent } from './nav/nav.component';
import { NoContentComponent } from './no-content';
import { HeadComponent } from './head/head.component';
import { NewssuscriptionComponent } from './newssuscription/newssuscription.component';
import { FooterComponent } from './footer/footer.component';
import { ParallaxComponent } from './parallax/parallax.component';
import { PopupComponent } from './popup/popup.component';
import { PopupService } from './popup/popup.service';
import { ContactoComponent } from './contacto/contacto.component';
import { SlideComponent } from './slide/slide.component';
import { CarouselComponent } from './carousel/carousel.component';
import { MapComponent } from './map/map.component'; // GoogleMap
import { GaleryComponent } from './galery/galery.component'; // GaleryComponent
import { NosotrosComponent } from './custom/nosotros.component'; // Custom
import { HacemosComponent } from './custom/hacemos.component'; // Custom
import { ProyectsComponent } from './proyects/proyects.component'; // ProyectsComponent


// External libs
import {
  TranslateModule, TranslateLoader,
  TranslateStaticLoader, TranslateService
} from 'ng2-translate';
import { SlimScrollModule } from 'ng2-slimscroll';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
import { AgmCoreModule } from 'angular2-google-maps/core';

// ambiente
import { Ambiente, ApiContacto, FilterByPipe, SafePipe, ParallaxDirective } from './shared';

import '../styles/styles.scss';
// Application wide providers
const APP_PROVIDERS = [
  ...APP_RESOLVER_PROVIDERS,
  AppState, ApiContacto, PopupService
];

type StoreType = {
  state: InternalStateType,
  restoreInputValues: () => void,
  disposeOldHosts: () => void
};

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: Http) {
  return new TranslateStaticLoader(http, 'assets/i18n', '.json');
}

/**
 * `AppModule` is the main entry point into Angular2's bootstraping process
 */
@NgModule({
  bootstrap: [AppComponent],
  declarations: [
    AppComponent,
    NoContentComponent,
    NavComponent,
    FooterComponent,
    ParallaxDirective,
    ParallaxComponent,
    PopupComponent,
    ContactoComponent,
    SlideComponent,
    CarouselComponent,
    NewssuscriptionComponent,
    HeadComponent,
    FilterByPipe,
    SafePipe,
    MapComponent,
    GaleryComponent,
    ProyectsComponent,
    NosotrosComponent, // Custom
    HacemosComponent // Custom
  ],
  imports: [ // import Angular's modules
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    RouterModule.forRoot(ROUTES, { useHash: true, preloadingStrategy: PreloadAllModules }),
    Ng2PageScrollModule.forRoot(),
    TranslateModule.forRoot({
      provide: TranslateLoader,
      useFactory: HttpLoaderFactory,
      deps: [Http]
    }),
    SlimScrollModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyDwV_LyQXxHlm7fWarLzof76k0go8Xxiz4'
    })
  ],
  providers: [ // expose our Services and Providers into Angular's dependency injection
    ENV_PROVIDERS,
    APP_PROVIDERS
  ]
})
export class AppModule {

  constructor(
    public appRef: ApplicationRef,
    public appState: AppState,
    public translate: TranslateService
  ) {
    translate.setDefaultLang('ES');
  }
  public hmrOnInit(store: StoreType) {
    if (!store || !store.state) {
      return;
    }
    console.log('HMR store', JSON.stringify(store, null, 2));
    // set state
    this.appState._state = store.state;
    // set input values
    if ('restoreInputValues' in store) {
      let restoreInputValues = store.restoreInputValues;
      setTimeout(restoreInputValues);
    }

    this.appRef.tick();
    delete store.state;
    delete store.restoreInputValues;
  }

  public hmrOnDestroy(store: StoreType) {
    const cmpLocation = this.appRef.components.map((cmp) => cmp.location.nativeElement);
    // save state
    const state = this.appState._state;
    store.state = state;
    // recreate root elements
    store.disposeOldHosts = createNewHosts(cmpLocation);
    // save input values
    store.restoreInputValues = createInputTransfer();
    // remove styles
    removeNgStyles();
  }

  public hmrAfterDestroy(store: StoreType) {
    // display new elements
    store.disposeOldHosts();
    delete store.disposeOldHosts;
  }

}



