import { Component, OnInit, AfterViewInit } from '@angular/core';
@Component({
  selector: 'my-head',
  template: require('./head.component.html'),
  styles: [require('./head.component.scss')]
})
export class HeadComponent implements OnInit, AfterViewInit {
  public small;
  public ngOnInit() {
    console.log('Init HeaderComponent');
    window.document.addEventListener('scroll', (e) => this.detectScroll(e));
  }
  public ngAfterViewInit() {
    console.log('AfterViewInit HeaderComponent');
  }
  public detectScroll(e) {
    let scrollTop;
    if (window.pageYOffset !== undefined) {
      scrollTop = window.pageYOffset;
    } else {
      scrollTop = document.body.scrollTop;
    }
    console.log(scrollTop);
    if (scrollTop > 220) {
      this.small = true;
      document.querySelector('body').classList.add('smallPadding');
    } else {
      this.small = false;
      document.querySelector('body').classList.remove('smallPadding');
    }
  }
}



