/*
 * Angular 2 decorators and services
 */
import {
  Component,
  OnInit,
  ViewEncapsulation
} from '@angular/core';
import { AppState } from './app.service';

/*
 * App Component
 * Top Level Component
  <div  class="icon-scroll-down">
    <a pageScroll href="#colchon"><i class="fa fa-angle-down" aria-hidden="true"></i></a>
  </div>
  <my-colchon></my-colchon>
  <my-estructura></my-estructura>
  <my-parallax></my-parallax>
  <my-pedido></my-pedido>
  <my-footer></my-footer>
 */
@Component({
  selector: 'app',
  encapsulation: ViewEncapsulation.None,
  template: `
  <my-head></my-head>  
  <my-slide [slides]="slides"></my-slide>
  <my-nav></my-nav>
  <my-nosotros></my-nosotros>
  <my-hacemos></my-hacemos>  
  <my-galery></my-galery>
  <my-proyects></my-proyects>
  <my-map></my-map>
  <my-footer></my-footer>
  <router-outlet></router-outlet>
  `
})
export class AppComponent implements OnInit {
  public slides = [
        {
            link: 'assets/images/slide1.jpg',
            title: 'SLIDE.SLIDE_1',
            style: ''
        },{
            link: 'assets/images/slide2.jpg',
            title: 'SLIDE.SLIDE_1',
            style: ''
        }
    ];
  constructor(
    public appState: AppState
  ) {}

  public ngOnInit() {
    console.log('Initial App State', this.appState.state);
  }

}

